        LineaTiempoD.color-primario
          div(numero="1" titulo="Planificacin de la produccin")   
            .row.justify-content-center
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema3/img03.png')   
              .col-lg-7
                p.mb-4 Est determinada por la unidad a vender en un periodo de tiempo determinado, con base en esta informacin se analiza la capacidad instalada de la planta, en cuanto a maquinaria, capital humano, tiempos de produccin, suministro de materiales, tiempo de las operaciones, distribucin de los mdulos, segn  complejidad de las prendas a fabricar. 
                p.mb-0 La organizacin de las plantas de produccin est determinada por el flujo del proceso productivo, la flexibilidad en la ubicacin de los puestos de trabajo, el flujo continuo de las operaciones, la comodidad de los trabajadores, la iluminacin y aireacin en beneficio del trabajador, las distancias en cada una de las operaciones, y la funcionalidad de manera efectiva.
          div(numero="2" titulo="Organizacin por mdulos (Celdas) de produccin en confecciones")           
            p.mb-4 La manufactura modular o celda de produccin es la forma de organizacin ms utilizada en plantas de produccin en confecciones, es un concepto desarrollado por Toyota en los aos 40's, su principio fundamental es el justo a tiempo, la reduccin de inventarios, flexibilidad en los cambios de referencia, reduccin de los costos y ciclos de produccin a partir de la optimizacin de los tiempos  y la mejora de los indicadores de productividad. 
            .row.justify-content-center            
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema3/img04.png')   
              .col-lg-7
                p.mb-4 El mdulo o celda de produccin se organiza de manera lineal de acuerdo con el orden operacional definido para el producto(vestuario), en un puesto de trabajo se realiza una o dos operaciones segn el tipo de maquinaria, tiempo de produccin y habilidad del operario.  Se tienen mesas auxiliares para descargar la produccin terminada que ser entregada al operario siguiente. 
                p.mb-0 Este sistema de produccin se caracteriza por lotes de productos de una misma serie o referencia, por lo general son pequeos en el nmero de unidades, debido a las tendencias de la moda segn la temporada. Su forma de organizacin es en pequeos mdulos de produccin conformado entre 3 y 6 operarios, cada uno encargado de una o varias operaciones.
          div(numero="3" titulo="El flujo de produccin")
            .row.justify-content-center
              .col-lg-5
                figure
                  img(src='@/assets/curso/tema3/img05.png')   
              .col-lg-7
                p.mb-4 El flujo de produccin en los sistemas modulares es en forma de U de acuerdo con el orden operacional definido en la ficha tcnica del producto(vestuario), En ocasiones quien rota es el operario segn su habilidad y conocimiento en la operacin y manejo de la maquinaria. 
                p.mb-0 Para su implementacin se deben tener en cuenta aspectos como cohesin del grupo, definicin de normas y roles, determinar los objetivos de produccin para el mdulo, distribucin y asignacin de tarea y cargas de trabajo, compresin de la interrelacin de sus operaciones y operarios. <b>La meta de uno es la meta de todos.</b>                            
        
