              p.mb-0 La ficha tcnica es un documento cuya funcin principal es informar de una manera estandarizada y sencilla las caractersticas tcnicas de su producto, describiendo las operaciones, tiempos, maquinaria, las imgenes del producto, las lupas, las observaciones de diseo o calidad   y normas tcnicas requerida para la elaboracin de este.   Esta informacin tiene utilidad a nivel comercial y productivo. La ficha tcnica cuenta con el orden especfico en que se va realizando la prenda: diseo, patronaje, trazo, corte, tiqueteado, confeccin, y terminacin. 
