    p.mb-5 La maquinaria utilizada en el proceso productivo de confeccin tiene varias caractersticas, entre ellas est el tipo de tecnologa que va desde primera generacin (mquinas mecnicas), segunda (maquina elctricas), tercera (electrnicas)  y cuarta generacin (autmatas), cada mquina ofrece  ventajas para el confeccionista, razn por la cual  una persona que quiera trabajar en el mundo de la moda debe adquirir este conocimiento, porque le permitir ser ms creativo y realista en el desarrollo de sus prendas. Las mquinas ms utilizadas en los diferentes procesos de confeccin son: maquina plana, maquina plana dos agujas, maquina fileteadora, maquina recubridora y maquina presilladora.

