    h3 Mtodos y tiempos    
    p.mb-4 En la industria de la confeccin hablar de rapidez en los procesos permite pensar siempre que hay una forma correcta para hacer las cosas y a su vez eficiente, es aqu donde los conceptos de mtodos y tiempos toman poder.
    p.mb-5 En cualquier actividad que se realice y ms an en el desarrollo de colecciones, se necesita utilizar mtodos adecuados para las diferentes operaciones y permitir ejecutarlas con mayor precisin, esta habilidad se desarrolla a travs del conocimiento que se adquiere por el trabajo repetitivo; el saber estandarizar un proceso o una prenda de vestir permite identificar fechas de entrega y poder adquirir compromisos con los clientes internos y externos de la compaa.          

